
import Swal from 'sweetalert2';
import 'sweetalert2/dist/sweetalert2.min.css';
import React, { useState,useEffect } from 'react';
import { useHistory } from 'react-router-dom';

import './index.css'; // Assuming you have a separate CSS file for styling


const NextPage = () => {
  const [enteredNumber, setEnteredNumber] = useState('');
  const [isIncorrect, setIsIncorrect] = useState(false);
  const history = useHistory();
  let isSirenPlaying = true;


  useEffect(() => {
    Swal.fire({
      title: 'GATE-1 UNLOCKED',
      text: 'Enter the Password to Unlock Gate-2.',
      icon: 'success',
      backdrop: false,
      buttonsStyling: false,
      customClass: {
        container: 'custom-alert-container',
        confirmButton: 'custom-ok-button',
      },
    });
  }, []);


  const handleNumberClick = (number) => {
    playDialTone(); 
    const newNumber = enteredNumber + number;
    setEnteredNumber(newNumber);
  };
 
  const handleEnterClick = () => {
     document.getElementById("auth2").style.display ="block";
     
    enter();
   setTimeout(()=>{
    if (enteredNumber === 'issathfarsana') {
      
      history.push('/LastNfinalPage');
      
    } else {
      document.getElementById("auth1").style.display ="block";
      document.getElementById("auth2").style.display ="none";
     
      
      setIsIncorrect(true);
      setEnteredNumber('');
  
        playSiren();
    
     
    }
  },4000);
 
  };
  const stopPlaySiren = () => {
    isSirenPlaying = false;
  };
  const enter = () => {
    var enterAudio = new Audio("data:audio/wav;base64,");
    enterAudio.play();
  }
  const playDialTone = () => {
     var snd = new Audio("data:audio/wav;base64,");
     snd.play();
  };
  const playSiren = () => {
    var siren = new Audio("data:audio/wav;base64,");
    siren.play();
 };
  function reset(){
    setIsIncorrect(false);
    setEnteredNumber('');
    window.location.reload();
  }
  return (
    <div className="container">
      <div className="row">
        <div className="col">
        <h1 style={{textAlign:'center',}}>GATE - <span style={{color:'white'}}>2</span>/3</h1>
        
        <div id='auth2' style={{textAlign:'center',fontWeight:'bold',color:'green',display:'none'}}>AUTHENTICATING...</div>
        <div id="auth1" style={{textAlign:'center'}}>UN-AUTHORIZED USER!!</div>
          <div className="calculator-container">
               <div>
               
            <div  className={`display ${isIncorrect ? 'redBackground' : ''}`}>{enteredNumber}</div><br></br>
            
            <div style={{ display: 'flex', alignItems: 'center' }}>
  <img style={{ width: '80px', alignSelf: 'flex-start', marginRight: '10px' }} src='https://i.ibb.co/YRWKBb4/Whats-App-Image-2023-05-23-at-10-34-26-AM.jpg' alt="Image" />
  <div className='green' style={{ textAlign: 'right', color: 'white', marginLeft: 'auto' }} onClick={handleEnterClick}>OPEN</div>
</div>

            </div>
          

            <div className="keypad">
           
              <div className="row">
                
                <button className="col btn btn-primary" onClick={() => handleNumberClick('i')}>
                  i
                </button>
                <button className="col btn btn-primary" onClick={() => handleNumberClick('f')}>
                  f
                </button>
                <button className="col btn btn-primary" onClick={() => handleNumberClick('a')}>
                  a
                </button>
              </div>
              <div className="row">
                <button className="col btn btn-primary" onClick={() => handleNumberClick('t')}>
                  t
                </button>
                <button className="col btn btn-primary" onClick={() => handleNumberClick('h')}>
                  h
                </button>
                <button className="col btn btn-primary" onClick={() => handleNumberClick('s')}>
                  s
                </button>
              </div>
              <div className="row">
                <button className="col btn btn-primary" onClick={() => handleNumberClick('r')}>
                  r
                </button>
                <button className="col btn btn-primary" onClick={() => handleNumberClick('n')}>
                  n
                </button>
                <button className="col btn btn-primary" onClick={() => handleNumberClick('@')}>
                  @
                </button>
              </div>
              <div className="row">
                <button className="col btn btn-primary zero-btn" onClick={() => handleNumberClick('123')}>
                  123
                </button>
                <button onClick={reset}  className="col btn btn-primary enter-btn">
                  Reset
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default NextPage;